export default "/*\r\n====================================             Parallax Background Container\r\n*/\r\n/*\r\nyou can control the height and width of this container\r\nchanging values in this class may affect other containers within\r\n*/\r\n.parallax-container{\r\n    height: 300px;\r\n    z-index: 0;\r\n}\r\n/*\r\nparallax-container animation class\r\n*/\r\n.stretch{\r\n    -webkit-animation: stretch 0.3s cubic-bezier(0, 0.42, 0, 1.29);\r\n            animation: stretch 0.3s cubic-bezier(0, 0.42, 0, 1.29);\r\n    -webkit-animation-fill-mode: forwards;\r\n            animation-fill-mode: forwards;\r\n}\r\n/*\r\n------------------- the parallax element\r\n*/\r\n.parallax{\r\n    /* The image used */\r\n    /*https://www.hdwallpapers.in/walls/mr_robot_hd_4k_8k-HD.jpg*/\r\n    background-image:url('Flooded_Hangar.jpg');\r\n    \r\n    /* Create the parallax scrolling effect */\r\n    background-attachment: fixed;\r\n    background-position: center;\r\n    background-repeat: no-repeat;\r\n    background-size: cover;\r\n    -webkit-user-select: none;\r\n       -moz-user-select: none;\r\n            user-select: none;\r\n}\r\n/*\r\n--------------------Div that holds the account name\r\n*/\r\n/*\r\nYou can control the height of the div\r\nas well as the position\r\n*/\r\n.acc-name-bg{\r\n    width: auto;\r\n    height: 40px;\r\n    position: absolute;\r\n    bottom: 0px;\r\n    border-top-right-radius: 10px;\r\n}\r\n/*\r\ndisplays the account name\r\ncan edit the font type as well as the size\r\n*/\r\n.acc-name{\r\n    font-family: 'Montserrat', sans-serif;\r\n    font-weight: 800;\r\n    font-size: 28px;\r\n    text-transform: uppercase;\r\n    text-shadow:\r\n        -1px -1px 0 #2469BB,  \r\n        1px -1px 0 #2469BB,\r\n        -1px 1px 0 #2469BB,\r\n        1px 1px 0 #2469BB;\r\n}\r\n.acc-name .material-icons{\r\n    font-size: 40px;\r\n}\r\n.parallax .row{\r\n    padding: 0px;\r\n    margin: 0;\r\n}\r\n/*\r\n=============================           Main account container\r\n*/\r\n/*\r\n-------------------   contains all elements under the bg image\r\n*/\r\n/*\r\nseperate from the background image container for the users name\r\n*/\r\n.acc-body{\r\n    width: auto;\r\n    height: 40%;\r\n}\r\n.tab-links{\r\n    position: absolute;\r\n    right: 20px;\r\n    bottom: 0;\r\n    text-align: right;\r\n    display: flex;\r\n}\r\n.tab{\r\n    height: 100%;\r\n    background-color: #FFF;\r\n    padding: 6px 20px 0px 20px;\r\n    border-radius: 30% 10% 0% 0%;\r\n    display: inline-block;\r\n}\r\n.tab:hover{\r\n    -webkit-animation: pop 0.2s ease-in;\r\n            animation: pop 0.2s ease-in;\r\n    -webkit-animation-fill-mode: forwards;\r\n            animation-fill-mode: forwards;\r\n    cursor: pointer;\r\n}\r\n.tab.current{\r\n    background-color: #000;\r\n}\r\n.tab-content{\r\n    display: none;\r\n    padding: 15px;\r\n}\r\n.tab-content.current{\r\n    display: inherit;\r\n}\r\n/*\r\nlabel for sections on acc page\r\n*/\r\n.label{\r\n    font-size: 20px;\r\n    text-transform: uppercase;\r\n}\r\n.holder{\r\n    width: 100%;\r\n    height: 100%;\r\n}\r\n.acc-body .section{\r\n    max-width: 100%;\r\n    margin-top: 20px;\r\n    padding: 10px;\r\n}\r\n/*\r\n=========================================      Animations\r\n*/\r\n/*\r\n-------------------Pop effect tabs\r\n*/\r\n@-webkit-keyframes pop{\r\n      100%  {transform: scale(1.2); background-color: #000;}\r\n}\r\n@keyframes pop{\r\n      100%  {transform: scale(1.2); background-color: #000;}\r\n}\r\n/*\r\n-------------------Stretch effect parallax\r\n*/\r\n@-webkit-keyframes stretch{\r\n    100% {    \r\n        z-index: 10; \r\n        width: 100%; \r\n        height: 100%; \r\n        position: fixed;\r\n        top: 0;\r\n        left: 0;\r\n    }\r\n}\r\n@keyframes stretch{\r\n    100% {\r\n        z-index: 10;\r\n        width: 100%;\r\n        height: 100%;\r\n        position: fixed;\r\n        top: 0;\r\n        left: 0;\r\n    }\r\n}\r\n/*\r\n================================================ Screen size (1024px)\r\n*/\r\n@media only screen and (max-width: 1024px) {\r\n/*\r\n=============================           Main account container\r\n*/\r\n/*\r\n-------------------   contains all elements under the bg image\r\n*/\r\n/*\r\nseperate from the background image container for the users name\r\n*/\r\n    .acc-body{\r\n        height: 60%;\r\n    }\r\n\r\n    .holder{\r\n        width: 100%;\r\n        height: auto;\r\n        overflow-x: auto;\r\n        overflow-y: auto;\r\n    }\r\n/*\r\n    ====================================     recent activity container.\r\n*/\r\n/*\r\n---------------holds the recent activity messages\r\n*/\r\n}\r\n/*\r\n================================================ Screen size (768px)\r\n*/\r\n@media only screen and (max-width: 768px) {\r\n    \r\n    .tabs-mobile {\r\n        width: 100%;\r\n        padding: 0%;\r\n        display: flex;\r\n    }\r\n    .tabs-mobile .tab {\r\n        flex:auto;\r\n        text-align: center;\r\n        border-radius: 0;\r\n    }\r\n    .tabs-mobile .tab:hover{\r\n        background-color: #000;\r\n        -webkit-animation: none;\r\n                animation: none;\r\n        cursor: pointer;\r\n    }\r\n\r\n}\r\n\r\n/*# sourceMappingURL=data:application/json;base64, */";