export default "/*\r\n---------------------      parallax customization\r\n*/\r\n.parallax {\r\n    /* The image used */\r\n    background-image:url('Flooded_Hangar.jpg');\r\n\r\n    /* Full height */\r\n    height: 500px;\r\n\r\n    /* Create the parallax scrolling effect */\r\n    background-attachment: fixed;\r\n    background-position: center;\r\n    background-repeat: no-repeat;\r\n    overflow: hidden;\r\n}\r\n/*\r\nthe overlapping element on the parallax\r\n*/\r\n.parallax .overLap{\r\n    width: 100%;\r\n    height: 100%;\r\n    background-color: rgba(0,0,0, 0.6);\r\n    margin: auto;\r\n    position: relative;\r\n}\r\n/*\r\nCustomization of button on\r\n*/\r\n.parallax .overLap .btn.LPink_Text.White_BG{\r\n    position: absolute;\r\n    bottom: 30px;\r\n    left: 47%;\r\n    height: 60px;\r\n    padding: 15px 30px;\r\n}\r\n@media only screen and (max-width: 320px){\r\n    .parallax{\r\n        height: 300px !important;\r\n    }\r\n}\r\n/*\r\n======================      Max screen width 1024px laptop size    =================\r\n*/\r\n@media only screen and (max-width: 1024px){\r\n    /*  classes   */\r\n    \r\n    /*Parallax*/\r\n    .parallax{\r\n        height: 400px;\r\n    }\r\n}    \r\n/*# sourceMappingURL=data:application/json;base64, */";